.login-box {
  position: absolute;
  top: 50%;
  left: 70%;
  width: 400px;
  padding: 40px;
  transform: translate(-50%, -50%);
  background: rgba(0, 0, 0, 0.5);
  box-sizing: border-box;
  box-shadow: 0 15px 25px rgba(0, 0, 0, 0.6);
  border-radius: 10px;
}

.login-box h2 {
  margin: 0 0 30px;
  padding: 0;
  color: #fff;
  text-align: center;
}

.login-box .user-box {
  position: relative;
}

.login-box .user-box input {
  width: 100%;
  padding: 10px 0;
  font-size: 16px;
  color: #fff;
  margin-bottom: 30px;
  border: none;
  border-bottom: 1px solid #fff;
  outline: none;
  background: transparent;
}

.login-box .user-box label {
  position: absolute;
  top: 0;
  left: 0;
  padding: 10px 0;
  font-size: 16px;
  color: #fff;
  pointer-events: none;
  transition: 0.5s;
}

.login-box .user-box input:focus ~ label,
.login-box .user-box input:valid ~ label {
  top: -20px;
  left: 0;
  color: #4CE0F4;
  font-size: 12px;
}

.forgotPassword {
  bottom: 40%;
  color: gray;
  cursor: pointer;
  display: block;
  font-size: 75%;
  left: 35px;
  opacity: 0.6;
  padding: 0.5rem;
  position: absolute;
  text-align: left;
  width: 100%;
}
.forgotPassword:hover-underline-animation:after {
  position: absolute;
  width: 100%;
  transform: scaleX(0);
  height: 2px;
  bottom: 0;
  left: 0;
  background-color: blue;
  transform-origin: bottom right;
  transition: transform 0.25s ease-out;
}

.login-box form a {
  color: white;
  text-decoration: none;
}

.login-box form button {
  border: none;
  background: transparent;
  position: relative;
  display: inline-block;
  padding: 10px 20px;
  font-size: 16px;
  text-transform: uppercase;
  overflow: hidden;
  transition: 0.5s;
  margin-top: 40px;
  letter-spacing: 4px;
}

.login-box button:hover {
  background: #D784F7;
  color: #fff;
  border-radius: 5px;
  box-shadow: 0 0 2px #D784F7, 0 0 23px #D784F7, 0 0 48px #D784F7, 0 0 98px #D784F7;
}

@media screen and (max-width: 991.98px) {
  body {
    background: linear-gradient(#e66465, #9198e5);
  }
  .login-box {
    left: 50%;
  }
}
@media screen and (max-width: 576px) {
  body {
    background: linear-gradient(#e66465, #9198e5);
  }
  .login-box {
    left: 50%;
    width: 350px;
    padding: 20px;
  }
}
.nombre-pagina {
  margin-top: 5rem;
  text-align: center;
}

.descripcion-pagina {
  text-align: center;
  font-size: 1.5rem;
}

.salir {
  text-decoration: none; /* Quitar subrayado del enlace */
  cursor: pointer;
}